.user {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding-top: 80px;
  align-items: center;
  background-color: #f6fafd;
  box-sizing: border-box;
}
.form {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 26%;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  background-color: white;
}
.reg-btn {
  margin-top: 20px;
  padding: 9px 0;
  background-color: #4896de;
  color: white;
  border: none;
  outline: none;
  border-radius: 5px;
  font-size: 15px;
  font-weight: 600;
}
.form-ip {
  padding: 5px 5px;
  border: none;
  outline: none;
  border-bottom: 1px solid grey;
  margin-top: 8px;
  margin-bottom: 12px;
  font-size: 16px;
}
.opt {
  font-size: 15px;
  color: grey;
}
.user-Text {
  font-size: 20px;
  text-align: center;
  font-weight: 550;
  margin-bottom: 10px;
}

/* Media */
@media screen and (max-width: 1024px) {
  .form {
    width: 40%;
  }
}
@media screen and (max-width: 750px) {
  .form {
    width: 50%;
  }
}
@media screen and (max-width: 550px) {
  .form {
    width: 100%;
  }
}
